import OutlinedInput from '@material-ui/core/OutlinedInput';
import FilledInput from '@material-ui/core/FilledInput';
import InputLabel from '@material-ui/core/InputLabel';

const styles = theme => ({
    root: {
        display: 'flex',
    },
    paper: {
        padding: theme.spacing.unit * 2,
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: '100%',
    }
});

const classes = theme => ({
    paper: {
        position: 'absolute',
        width: 400,
        backgroundColor: theme.palette.background.paper,
        border: '2px solid #000',
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
    },
});


const TimeSeriesContainer = (props) => {
    const [addSerieModalState, setAddSerieModalState] = useState(false);
    const [serieName, setSerieName] = useState("");
    const [plotPoints, setPlotPoints] = useState([]);
    const [selectedSerie, setSelectedSerie] = useState("");
    const [viewType, setViewType] = useState("");
    const [charFormat, setCharFormat] = useState(["x", "data", "forecast"]);
    const [enodo_models, setEnodo_models] = useState(null);
    const [selectedModel, setSelectedModel] = useState(null);

    const [series, set_series] = useGlobal();

    const removeTimeserieFromAnalyser = (serieName) => {
        console.log(this.state.rightClickSelectedSerie);
        // ReactPubSubStore.publish('/series/' + serieName, {}, "DELETE", (data) => {
        //     console.log(data);
        //     ReactPubSubStore.update('/series');
        // });
    };

    const showChart = (serie) => {
        // ReactPubSubStore.publish('/series/' + serie.name, {}, "GET", (data) => {
        socket.emit('/api/series/details', {serie_name: serie.name}, (status, data, message) => {
            console.log(data);
            // let points = data.points.concat(data.forecast_points);


            let points = [];
            if (serie.analysed) {
                let history = data.points;
                // let history = data.points.slice(Math.max(data.points.length - (data.forecast_points.length * 100), 1));
                for (let i = 0; i < (history.length + data.forecast_points.length); i++) {
                    if (i < history.length) {
                        points.push([new Date(history[i][0]), history[i][1], null]);
                    } else {
                        points.push([new Date(data.forecast_points[i - history.length][0]), null, data.forecast_points[i - history.length][1]]);
                    }
                }
            } else {
                let history = data.points;
                for (let i = 0; i < (history.length); i++) {
                    points.push([new Date(history[i][0]), history[i][1]]);
                }
            }
            this.setState({
                selectedSerie: serie.name,
                viewType: "graph",
                plotPoints: points,
                charFormat: serie.analysed ? ["x", "data", "forecast"] : ["x", "data"]
            });
        });
    };

    const getModalStyle = () => {
        const top = 50;

        return {
            top: `${top}%`,
            left: 0,
            right: 0,
            margin: 'auto',
            position: 'absolute',
            minWidth: '300px',
            maxWidth: '700px',
        };
    };

    const handleModalChange = (event) => {
        let obj = {};
        obj[event.target.name] = event.target.value;
        this.setState(obj);
    };


    console.log(this.state);
    const modalStyle = this.getModalStyle();
    console.log(modalStyle);
    let chartData = [this.state.charFormat];
    chartData = chartData.concat(this.state.plotPoints);
    return (
        <div className="">
            <Grid container spacing={24}>
                <Grid item xs={12}>
                    <Paper className={styles.paper}>
                        <div style={{margin: "20px", padding: "10px 0"}}>
                            <div style={{textAlign: "right"}}>
                                <button type="button" className="btn btn-primary" onClick={() => {
                                    this.setState({addSerieModalState: true})
                                }}>Add
                                </button>
                            </div>
                            <List>
                                {this.state.series && this.state.series.map((serie) => {
                                    return (
                                        <ListItem>
                                            <ListItemText
                                                primary={serie.name}
                                                secondary={`Analysed: ${(serie.analysed === true ? "yes" : "no")}`}
                                            />
                                            <ListItemSecondaryAction>
                                                <IconButton aria-label="Show info"
                                                            onClick={() => {
                                                                this.setState({
                                                                    viewType: 'info',
                                                                    selectedSerie: serie.name
                                                                });
                                                            }}>
                                                    <InfoIcon/>
                                                </IconButton>
                                                <IconButton onClick={() => {
                                                    this.showChart(serie);
                                                }} aria-label="Show graph">
                                                    <ShowChartIcon/>
                                                </IconButton>
                                                <IconButton aria-label="Delete">
                                                    <DeleteIcon/>
                                                </IconButton>
                                            </ListItemSecondaryAction>
                                        </ListItem>);
                                })}
                            </List>
                        </div>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper className={styles.paper}>

                        {(this.state.selectedSerie === "" || this.state.selectedSerie === null) && "Select a serie"}
                        {this.state.viewType === "graph" &&
                        <Chart
                            chartType="LineChart"
                            data={chartData}
                            options={{
                                backgroundColor: "#424242",
                                explorer: {
                                    actions: ['dragToZoom', 'rightClickToReset'],
                                    axis: 'horizontal',
                                    keepInBounds: true,
                                    maxZoomIn: 4.0
                                },
                            }}
                            width="100%"
                            height="400px"
                            legendToggle
                        />
                        }
                        {this.state.viewType === "info" &&
                        <Info serie={this.state.selectedSerie}/>
                        }
                    </Paper>
                </Grid>
            </Grid>
            <ContextMenu id="SIMPLE">
                <MenuItem data={{}} onClick={() => {
                    this.removeTimeserieFromAnalyser(this.state.rightClickSelectedSerie);
                }}>
                    Delete
                </MenuItem>
            </ContextMenu>
            <div className="row">
                {this.state.addSerieModalState &&
                <Modal
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    open={this.state.addSerieModalState}
                    onClose={() => {
                        this.setState({
                            addSerieModalState: false
                        });
                    }}>
                    <div style={modalStyle} className={classes.paper}>
                        <Paper className={styles.paper} style={{minWidth: '100%', padding: '20px'}}>
                            <h2 id="simple-modal-title">Add serie</h2>
                            <div>
                                <InputLabel htmlFor="age-simple">Age</InputLabel>
                                <Select
                                    style={{minWidth: '100%'}}
                                    value={this.state.selectedModel}
                                    onChange={(e) => {
                                        this.handleModalChange(e);
                                    }}
                                    inputProps={{
                                        name: 'selectedModel',
                                        id: 'selectedModel',
                                    }}>
                                    {Object.keys(this.state.enodo_models.models).map((model_id) => {
                                        console.log(model_id);
                                        return <MenuItem
                                            value={model_id}>{this.state.enodo_models.models[model_id]}</MenuItem>
                                    })}
                                </Select>
                            </div>
                            <FormControl className={styles.formControl}>
                                <button
                                    onClick={() => {
                                        this.setState({
                                            addSerieModalState: false
                                        });
                                        ReactPubSubStore.publish('/series', {
                                            name: this.state.serieName,
                                            unit: this.state.unit,
                                            m: this.state.m,
                                            d: this.state.d,
                                            D: this.state.D
                                        }, "POST", (data) => {
                                            console.log(data);
                                        });
                                    }}
                                />
                            </FormControl>
                        </Paper>
                    </div>
                </Modal>
                }
            </div>
        </div>
    );
}

export default TimeSeriesContainer;
